.hero {
    height: 100vh;
    position: relative;
    min-height: 630px;
    background-color: rgba($color: #2b2e26, $alpha: 0.5);
    &__video {
        height: 100%;
        width: 100%; }
    &__text {
        position: absolute;
        @include vcenter;
        color: #ffffff;
        text-align: center;
        max-width: 840px;
        min-width: 720px;
        @include sm {
            max-width: 740px;
            min-width: initial; } }
    &__title {
        margin-bottom: 18px;
        font-size: 35px;
        line-height: 0.94;
        text-transform: uppercase;
        font-weight: 400; }
    &__descr {
        font-size: 25px;
        font-weight: 300;
        line-height: 1.32; }
    // &__form
 }    //     max-width: 780px

