body {
  background: #FFF;
  line-height: 1.5;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
  font-size: 16px;
}

.title {
  font-size: 22px;
  font-weight: 300;
  text-align: center;
  color: #141414;
  text-transform: uppercase;
}

.divider {
  text-align: center;
  margin: 28px auto;
}

.icon-divider {
  display: block;
  margin: 0 auto;
  font-size: 1.6em;
}

.container-inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

@media only screen and (max-width: 1023px) {
  .container-inner {
    max-width: 1000px;
  }
}

@media only screen and (max-width: 767px) {
  .container-inner {
    max-width: 100%;
  }
}

.block {
  display: flex;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  padding-top: 50px;
}

.header__container {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: auto;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__item {
  color: #fff;
  margin-left: 57px;
}

.nav__link {
  font-size: 16px;
  font-weight: 300;
}

.nav__phone {
  font-size: 24px;
}

.logo {
  display: block;
}

.logo__text {
  display: inline-block;
  color: #fff;
  font-size: 67px;
  font-family: "Corinthia";
  font-weight: 400;
  line-height: 67px;
  vertical-align: middle;
}

.logo__img {
  display: inline-block;
  vertical-align: middle;
  margin-right: 30px;
}

.logo--vert {
  text-align: center;
}

.logo--vert .logo__text {
  color: #333333;
}

.logo--vert .logo__img {
  display: block;
  margin-right: 0;
  margin-bottom: 30px;
}

.hero {
  height: 100vh;
  position: relative;
  min-height: 630px;
  background-color: rgba(43, 46, 38, 0.5);
}

.hero__video {
  height: 100%;
  width: 100%;
}

.hero__text {
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #ffffff;
  text-align: center;
  max-width: 840px;
  min-width: 720px;
}

@media only screen and (max-width: 767px) {
  .hero__text {
    max-width: 740px;
    min-width: initial;
  }
}

.hero__title {
  margin-bottom: 18px;
  font-size: 35px;
  line-height: 0.94;
  text-transform: uppercase;
  font-weight: 400;
}

.hero__descr {
  font-size: 25px;
  font-weight: 300;
  line-height: 1.32;
}

.mb_YTPlayer:focus {
  outline: 0;
}

.YTPWrapper {
  display: block;
  transform: translateZ(0) translate3d(0, 0, 0);
  transform-style: preserve-3d;
  perspective: 1000;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  box-sizing: border-box;
}

.mb_YTPlayer .loading {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 12px;
  color: #fff;
  background: rgba(0, 0, 0, 0.51);
  text-align: center;
  padding: 2px 4px;
  border-radius: 5px;
  font-family: "Droid Sans", sans-serif;
  -webkit-animation: fade .1s infinite alternate;
  /* Chrome, Safari, Opera */
  animation: fade .1s infinite alternate;
}

/* Chrome, Safari, Opera */
@-webkit-keyframes fade {
  0% {
    opacity: .5;
  }
  100% {
    opacity: 1;
  }
}

/* Standard syntax */
@keyframes fade {
  0% {
    opacity: .5;
  }
  100% {
    opacity: 1;
  }
}

.YTPFullscreen {
  display: block !important;
  position: fixed !important;
  width: 100% !important;
  height: 100% !important;
  top: 0 !important;
  left: 0 !important;
  margin: 0 !important;
  border: none !important;
  opacity: 1 !important;
  background-color: #000 !important;
  padding: 0 !important;
}

.inlinePlayButton {
  font-family: 'ytpregular';
  font-size: 45px;
  color: #ffffff;
  text-align: center;
  border-radius: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  width: 80px;
  height: 80px;
  line-height: 80px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  cursor: pointer;
  opacity: .8;
  transition: all .4s;
  transform: scale(1.3);
}

.mb_YTPlayer:hover .inlinePlayButton {
  opacity: 1;
  transform: scale(1);
}

.inlinePlayButton:hover {
  background-color: black;
  transform: scale(1.3) !important;
}

.mbYTP_wrapper iframe {
  max-width: 8000px !important;
}

.inline_YTPlayer {
  margin-bottom: 20px;
  vertical-align: top;
  position: relative;
  left: 0;
  overflow: hidden;
  border-radius: 4px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
  background: rgba(0, 0, 0, 0.5);
}

.inline_YTPlayer img {
  border: none !important;
  margin: 0 !important;
  padding: 0 !important;
  transform: none !important;
}

/*CONTROL BAR*/
.mb_YTPBar .ytpicon {
  font-size: 20px;
  font-family: 'ytpregular';
}

.mb_YTPBar .mb_YTPUrl.ytpicon {
  font-size: 30px;
}

.mb_YTPBar {
  transition: opacity .5s;
  display: block;
  width: 100%;
  height: 10px;
  padding: 5px;
  background: #333;
  position: fixed;
  bottom: 0;
  left: 0;
  text-align: left;
  z-index: 1000;
  font: 14px/16px sans-serif;
  color: white;
  opacity: .1;
  box-sizing: border-box;
}

.mb_YTPBar.visible,
.mb_YTPBar:hover {
  opacity: 1;
}

.mb_YTPBar .buttonBar {
  transition: all .5s;
  background: transparent;
  font: 12px/14px san-serif;
  position: absolute;
  top: -30px;
  left: 0;
  padding: 5px;
  width: 100%;
  height: 40px;
  box-sizing: border-box;
}

.mb_YTPBar:hover .buttonBar {
  background: rgba(0, 0, 0, 0.4);
}

.mb_YTPBar span {
  display: inline-block;
  font: 16px/20px sans-serif;
  position: relative;
  width: 30px;
  height: 25px;
  vertical-align: middle;
}

.mb_YTPBar span.mb_YTPTime {
  width: 130px;
}

.mb_YTPBar span.mb_YTPUrl,
.mb_YTPBar span.mb_OnlyYT {
  position: absolute;
  width: auto;
  display: block;
  top: 6px;
  right: 10px;
  cursor: pointer;
}

.mb_YTPBar span.mb_YTPUrl img {
  width: 60px;
}

.mb_YTPBar span.mb_OnlyYT {
  left: 300px;
  right: auto;
}

.mb_YTPBar span.mb_OnlyYT img {
  width: 25px;
}

.mb_YTPBar span.mb_YTPUrl a {
  color: white;
}

.mb_YTPBar .mb_YTPPlayPause,
.mb_YTPlayer .mb_YTPBar .mb_YTPPlayPause img {
  cursor: pointer;
}

.mb_YTPBar .mb_YTPMuteUnmute {
  cursor: pointer;
}

/*PROGRESS BAR*/
.mb_YTPBar .mb_YTPProgress {
  height: 10px;
  width: 100%;
  background: #222;
  bottom: 0;
  left: 0;
}

.mb_YTPBar .mb_YTPLoaded {
  height: 10px;
  width: 0;
  background: #444;
  left: 0;
}

.mb_YTPBar .mb_YTPseekbar {
  height: 10px;
  width: 0;
  background: #bb110e;
  bottom: 0;
  left: 0;
  box-shadow: rgba(82, 82, 82, 0.47) 1px 1px 3px;
}

.mb_YTPBar .YTPOverlay {
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  -webkit-transform-style: "flat";
  box-sizing: border-box;
}

/*RASTER OVERLAY*/
.YTPOverlay.raster {
  background: url("images/raster.png");
}

.YTPOverlay.raster.retina {
  background: url("images/raster@2x.png");
}

.YTPOverlay.raster-dot {
  background: url("images/raster_dot.png");
}

.YTPOverlay.raster-dot.retina {
  background: url("images/raster_dot@2x.png");
}

/*VOLUME SLIDER*/
.mb_YTPBar .simpleSlider {
  position: relative;
  width: 100px;
  height: 10px;
  border: 1px solid #fff;
  overflow: hidden;
  box-sizing: border-box;
  margin-right: 10px;
  cursor: pointer !important;
  border-radius: 3px;
}

.mb_YTPBar.compact .simpleSlider {
  width: 40px;
}

.mb_YTPBar.compact span.mb_OnlyYT {
  left: 260px;
}

.mb_YTPBar .simpleSlider.muted {
  opacity: .3;
}

.mb_YTPBar .level {
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: #fff;
  box-sizing: border-box;
}

.mb_YTPBar .level.horizontal {
  height: 100%;
  width: 0;
}

.mb_YTPBar .level.vertical {
  height: auto;
  width: 100%;
}

.video {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  transform: translate(-50%, -50%);
}

.catering {
  margin-bottom: 95px;
}

.catering__options:after {
  content: " ";
  display: table;
  clear: both;
}

.catering__column {
  float: left;
  width: calc( 50% - 15px);
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .catering__column {
    float: none;
    width: 100%;
  }
}

.catering__column:first-child {
  margin-right: 30px;
}

.catering__item {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  margin-bottom: 30px;
  position: relative;
}

.catering__text {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  transform: scale(0.3);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.catering .image-big {
  padding-bottom: 70%;
}

.catering .image-middle {
  padding-bottom: 60%;
}

.catering .image-small {
  padding-bottom: 40%;
}

.about {
  margin-bottom: 60px;
}

.events__photos {
  margin: 0 -20px 0 -20px;
