}

.calculate__field {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 60px -28px;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .calculate__field {
    flex-flow: column;
  }
}

.calculate__input {
  display: block;
  flex-grow: 1;
  background-color: transparent;
  font-size: 16px;
  font-weight: 300;
  line-height: 2.06;
  color: #ffffff;
  padding: 12px 0;
  border-bottom: 3px solid #fff;
  margin: 0 28px;
}

.calculate__input::placeholder {
  background-color: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  color: inherit;
}

.calculate__input:focus {
